import React from "react";
import "../Intro.css"
import { Link } from "react-router-dom";

    function intro (){


    function setCountry() {

        var country = document.querySelector('#country');
        var countryValue = country.value ;

        return localStorage.setItem('country', countryValue);
    }

    function setId() {
        var input = document.querySelector('#name-input')
        var inputValue = input.value

        return localStorage.setItem('id', inputValue);
    }

    function setStatusLog() {
        var statusLog = true;

        return localStorage.setItem('statusLog', statusLog);
    }


    function inputValidation() {
        var input = document.querySelector('#name-input');
        var nome = /^[a-zA-Z\s]+$/;

        if (input.value.match(nome)) {
            setCountry()
            setId()
            setStatusLog()
            window.location.reload()
        } else {
            const holder = document.querySelector('#name-input').placeholder = 'Enter with a valid name';

            const clear = input.value = '';
            const color = input.classList.add('inputVal');
    
            return (clear, color)
        }

    }



    return (
        <span className="Register-Container">
        <div className="reg-box">
            <select id="country">
                <option value="BR">Brazil</option>
                <option value="US">USA</option>
                <option value="CA">Canada</option>


            </select>

            <input
                id="name-input"
                type="text"
                minLength={2}
                maxLength={25}
                placeholder="Enter with your name"

            />
        </div>

        <span className="reg-btn" onClick={inputValidation}><p>Subscribe</p></span>
    </span>
    )


}

export default intro
