import React from "react";
import "./RatingStars.css";
import "../LiveShow.jsx";

import FullStar from "./FullStar.svg";

 function RaitingStars (liveShow, props) {
  const rating = liveShow.rating;
  const ratingVotes= liveShow.ratingVotes;
  const usuario = props.usuario || {}

  return (
    <div className="ratingStars">

      <div className="StarsGroup">
        <img src={FullStar} alt="" />
        <p>{`${rating} / 10`}</p>
      </div>
      
      <p>{`👍🏼 ${ratingVotes} Votes`}</p>
    
    </div>
  )
}

export default RaitingStars