import React, { useState, useEffect } from "react";
import {APIKey} from '../../Services/APIKEY'
import { useParams } from "react-router-dom";

import "./LoginPage.css";
import Register from '../../Components/Header/Intro/Register/Register.jsx'

function LoginPage() {

  const logged = localStorage.getItem("statusLog");
  const image_path = "https://image.tmdb.org/t/p/original/";
  const [movies, setMovies] = useState([]);
  const { id } = useParams();

  useEffect(() => {
          fetch(`
          https://api.themoviedb.org/3/movie/${id}?language=en-US&api_key=${APIKey}`)
              .then((response) => response.json())
              .then((data) => {
              const movieImg = data;
              setMovies(movieImg);
          });
  }, []);


  return (
    <div className="LoginPage">
      <img src={`${image_path}${movies.backdrop_path}`} alt={movies.title} />
      <span className="LoginPageContent">
        <h1 >You need to
        <br />subscribe to enjoy</h1>
        <Register></Register>
      </span>
    </div>
  );
};

export default LoginPage;
