import React from "react";
import { Link } from "react-router-dom";
import { useState, useEffect } from "react";
import { APIKey } from "../../../Services/APIKEY";
import "./PopularMovies.css"



function PopularMoviesfunc() {
  const image_path = "https://image.tmdb.org/t/p/original/";
  const statusLog = localStorage.getItem("statusLog");
  const [movies, setMovies] = useState([]);

  useEffect(() => {
    fetch(`
    https://api.themoviedb.org/3/discover/movie?include_adult=true&include_video=true&language=en-US&page=1&sort_by=popularity.desc&api_key=${APIKey}`)
        .then((response) => response.json())
        .then((data) => {
        const moviesqtde = data.results
        moviesqtde.length = 10;
        setMovies(moviesqtde);
      });
  }, []);


  useEffect(() => {
    const PopularNextBtn = document.getElementById('Popular-nextbtn');
    const PopularPrevBtn = document.getElementById('Popular-prevbtn');
    const PopularSlideList = document.querySelector('.PopularList');


    const scrollar = PopularSlideList.offsetWidth / 1;


    PopularPrevBtn.addEventListener("click", function(){
        PopularSlideList.scrollLeft -= scrollar;
    })

    PopularNextBtn.addEventListener("click", function(){
        PopularSlideList.scrollLeft += scrollar;
    })



  }, []);




  return (
    <section className="popularcontent">
        <div className="PopularMoviesh1">
            <h1>Most Popular</h1>

            <span className="SeeAll">
                <h3>See all</h3>
                <span className="SeeAllSvg">
                    <svg
                    width="16"
                    height="17"
                    viewBox="0 0 16 17"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                    >
                    <path
                        d="M2.66536 9.24349L10.7787 9.24349L7.05203 12.9702L7.9987 13.9102L13.332 8.57682L7.9987 3.24349L7.0587 4.18349L10.7787 7.91016L2.66536 7.91016L2.66536 9.24349Z"
                        fill="#F41B3B"
                        stroke="#F41B3B"
                        strokeWidth="0.2"
                    />
                    </svg>
                </span>
            </span>
        </div>

        <div className="PopularContent">

            <div id="Popular-prevbtn">
            <svg width="18" height="30" viewBox="0 0 18 30" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M0.447213 15.9685L15.4472 29.6045C16.0329 30.1371 16.9827 30.1371 17.5685 29.6044C18.1543 29.0719 18.1543 28.2085 17.5684 27.676L3.62912 15.0042L17.5685 2.33172C18.1543 1.79918 18.1543 0.935815 17.5684 0.40327C17.2756 0.136998 16.8917 0.00390649 16.5078 0.00390649C16.1239 0.00390649 15.74 0.136998 15.4471 0.403362L0.447213 14.0401C0.165813 14.2958 0.00781264 14.6426 0.00781264 15.0043C0.00781264 15.3659 0.165813 15.7128 0.447213 15.9685Z" fill="white"/>
            </svg>
            </div>
          
            <div className="PopularInner">
                <div className="PopularList">
                      {movies.map((movie) => {
                          return (
                          <div className="PopularItem" key={movie.id}>
                            <div className="PopularCard-inner">
                                <div className="PopularCard-container">
                                        <div className="bg-filter"></div>
                                        <img src={`${image_path}${movie.poster_path}`} alt={movie.title} />

                                        <span className="PopularItemH1">
                                        <h1>{movie.title}</h1>
                                        </span>

                                        <Link to={`/BD-Screens/movie/${movie.id}`} >
                                            <span className="PopularCard-Btn">
                                                <svg width="66" height="67" viewBox="0 0 66 67" fill="none" xmlns="http://www.w3.org/2000/svg">
                                            <path d="M33 66.3181C51.0809 66.3181 65.7383 51.6253 65.7383 33.5008C65.7383 15.3764 51.0809 0.683594 33 0.683594C14.9192 0.683594 0.261719 15.3764 0.261719 33.5008C0.261719 51.6253 14.9192 66.3181 33 66.3181Z" fill="#F41B3B"/>
                                            <path d="M45.9701 31.1813L28.2369 20.9258C27.4184 20.3789 26.3271 20.3789 25.5086 20.9258C24.6902 21.4729 24.1445 22.2932 24.1445 23.2504V43.6244C24.1445 44.5816 24.6902 45.5388 25.5086 45.949C25.9179 46.2224 26.4635 46.3591 26.8727 46.3591C27.282 46.3591 27.8276 46.2224 28.2369 45.949L45.8337 35.6936C46.6522 35.1466 47.1978 34.3262 47.1978 33.369C47.1978 32.4118 46.7886 31.5915 45.9701 31.1813Z" fill="white" fillOpacity="0.87"/>
                                                </svg>
                                            </span>
                                        </Link> 

                                </div>
                                <div className="PopularInfo-Container">
                                    <h2>2017 • 2h 35m</h2>
                                    <h3>Romance/Drama</h3>
                                </div>

                                <div className="downloadCard">
                                    <svg width="15" height="16" viewBox="0 0 15 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                                    <path fillRule="evenodd" clipRule="evenodd" d="M14.2608 9.19922C14.6691 9.19922 15 9.53016 15 9.9384L15 12.8951C15 13.4832 14.7664 14.0473 14.3505 14.4632C13.9346 14.879 13.3706 15.1127 12.7825 15.1127L2.43394 15.1127C1.84581 15.1127 1.28177 14.879 0.865898 14.4632C0.450029 14.0473 0.216396 13.4832 0.216396 12.8951L0.216396 9.9384C0.216396 9.53016 0.547338 9.19922 0.955576 9.19922C1.36381 9.19922 1.69476 9.53016 1.69476 9.9384L1.69476 12.8951C1.69476 13.0912 1.77263 13.2792 1.91126 13.4178C2.04988 13.5564 2.23789 13.6343 2.43394 13.6343L12.7825 13.6343C12.9785 13.6343 13.1665 13.5564 13.3051 13.4178C13.4438 13.2792 13.5216 13.0912 13.5216 12.8951L13.5216 9.9384C13.5216 9.53016 13.8526 9.19922 14.2608 9.19922Z" fill="#1884F7"/>
                                    <path fillRule="evenodd" clipRule="evenodd" d="M11.8226 5.7165C12.1112 6.00517 12.1112 6.47319 11.8226 6.76186L8.12666 10.4578C7.83799 10.7464 7.36997 10.7464 7.0813 10.4578L3.3854 6.76186C3.09673 6.47319 3.09673 6.00517 3.3854 5.7165C3.67407 5.42783 4.14209 5.42783 4.43076 5.7165L7.60398 8.88972L10.7772 5.7165C11.0659 5.42783 11.5339 5.42783 11.8226 5.7165Z" fill="#1884F7"/>
                                    <path fillRule="evenodd" clipRule="evenodd" d="M7.60457 0.328125C8.01281 0.328125 8.34375 0.659067 8.34375 1.06731L8.34375 9.93747C8.34375 10.3457 8.01281 10.6766 7.60457 10.6766C7.19633 10.6766 6.86539 10.3457 6.86539 9.93747L6.86539 1.06731C6.86539 0.659067 7.19633 0.328125 7.60457 0.328125Z" fill="#1884F7"/>
                                    </svg>
                                </div>

                            </div>
                          </div>
                          );
                      })}
                  </div>
            </div>

            <div id="Popular-nextbtn">
            <svg width="18" height="30" viewBox="0 0 18 30" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M17.5606 14.0354L2.56057 0.399397C1.97487 -0.133148 1.02506 -0.133148 0.439263 0.399488C-0.146438 0.932033 -0.146438 1.7954 0.439363 2.32794L14.3787 14.9997L0.439263 27.6722C-0.146438 28.2047 -0.146438 29.0681 0.439363 29.6006C0.732164 29.8669 1.11606 30 1.49997 30C1.88387 30 2.26777 29.8669 2.56067 29.6005L17.5606 15.9638C17.842 15.7081 18 15.3613 18 14.9997C18 14.638 17.842 14.2911 17.5606 14.0354Z" fill="white"/>
            </svg>
            </div>
        </div>
    </section>
  );
}

export default PopularMoviesfunc