
import React from "react";
import "./NavLogged.css"
import Pesquisa from "../Search/Pesquisa";



var navmenu = (props) => {
    const clientid = localStorage.getItem('id')
    var loggout = () => {
        localStorage.setItem("statusLog", "false");
        localStorage.setItem("country", "");
        localStorage.setItem("id", "");
        window.location.reload();
    }


    var Abrirperfil = () => {
        const mostrar = document.getElementById("Card-Profile");
        const mostrar2 = document.getElementById("pesquisa-container");
        const esconder = document.getElementById("btn-search");

        mostrar2.classList.remove("activeSearch");
        esconder.classList.remove("activeSearch");
        mostrar.classList.toggle("ActiveProfile");
    }




    return (
        <div className="nav-container">

            <div className="signin-container">

                <Pesquisa></Pesquisa>

                <div className="Profile-Content" onClick={Abrirperfil} >
                    <span className="Profile-Avatar">
                        <img src="https://cdn.bfe.dev/bfe/img/YRkyVwrmKWqggtRUMjPIsIhsrfhyhfOs_800x800_1619063642899.png" alt="" />
                    </span>

                    <span className="mini-menu">
                        <svg width="14" height="14" viewBox="0 0 14 14" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M1.33271 3.748L6.86223 10.6799C6.95368 10.7943 7.14706 10.7943 7.23851 10.6799L12.7686 3.74752C12.8345 3.66461 12.8539 3.54103 12.8184 3.43345C12.8111 3.41106 12.8029 3.39374 12.7961 3.38098C12.7539 3.30276 12.6727 3.25391 12.5838 3.25391L1.51694 3.25391C1.42838 3.25391 1.34679 3.30264 1.30491 3.38098C1.29769 3.3941 1.28963 3.41142 1.28265 3.43224C1.24715 3.5409 1.26653 3.66485 1.33271 3.748Z" fill="white" />
                        </svg>
                    </span>
                </div>
            </div>

            <div id="Card-Profile">
                <div className="Card-avatar-container">
                    <div className="Card-avatar">
                        <img src="https://cdn.bfe.dev/bfe/img/YRkyVwrmKWqggtRUMjPIsIhsrfhyhfOs_800x800_1619063642899.png" alt="" />
                    </div>
                    <h5>{clientid}</h5>
                </div>

                <div className="Card-Profile-Flex">
                    <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M20 21V19C20 17.9391 19.5786 16.9217 18.8284 16.1716C18.0783 15.4214 17.0609 15 16 15H8C6.93913 15 5.92172 15.4214 5.17157 16.1716C4.42143 16.9217 4 17.9391 4 19V21" stroke="white" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                        <path d="M12 11C14.2091 11 16 9.20914 16 7C16 4.79086 14.2091 3 12 3C9.79086 3 8 4.79086 8 7C8 9.20914 9.79086 11 12 11Z" stroke="white" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                    </svg>

                    <p>Profile</p>
                </div>

                <div className="Card-Profile-Flex">
                    <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M19 19C19 19.5523 18.5523 20 18 20C17.4477 20 17 19.5523 17 19V17H15C14.4477 17 14 16.5523 14 16C14 15.4477 14.4477 15 15 15H17V13C17 12.4477 17.4477 12 18 12C18.5523 12 19 12.4477 19 13V15H21C21.5523 15 22 15.4477 22 16C22 16.5523 21.5523 17 21 17H19V19ZM12 16C12 16.5523 11.5523 17 11 17H3C2.44772 17 2 16.5523 2 16C2 15.4477 2.44772 15 3 15H11C11.5523 15 12 15.4477 12 16ZM15 12C15 12.5523 14.5523 13 14 13H3C2.44772 13 2 12.5523 2 12C2 11.4477 2.44772 11 3 11H14C14.5523 11 15 11.4477 15 12ZM15 8C15 8.55228 14.5523 9 14 9H3C2.44772 9 2 8.55228 2 8C2 7.44772 2.44772 7 3 7H14C14.5523 7 15 7.44772 15 8Z" fill="white" />
                    </svg>

                    <p>Watchlist</p>
                </div>

                <div className="Card-Profile-Flex">
                    <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path fillRule="evenodd" clipRule="evenodd" d="M21.9531 11.5151C21.9612 6.31597 17.7294 2.0632 12.5302 2.04692C9.34663 2.03698 6.52845 3.60111 4.80859 6.00406V3.33595C4.80859 2.95195 4.49728 2.64063 4.11328 2.64063H3.74219C3.35819 2.64063 3.04688 2.95195 3.04688 3.33595V8.6797C3.04688 9.06369 3.35819 9.37501 3.74219 9.37501H9.08594C9.46993 9.37501 9.78125 9.06369 9.78125 8.6797V8.3086C9.78125 7.92461 9.46993 7.61329 9.08594 7.61329H5.86145C7.19732 5.33569 9.67062 3.8086 12.5 3.8086C16.765 3.8086 20.1914 7.26797 20.1914 11.5C20.1914 15.765 16.732 19.1914 12.5 19.1914C10.5207 19.1914 8.71852 18.4454 7.35656 17.2196C7.08149 16.9721 6.66146 16.985 6.4009 17.2455L6.13857 17.5078C5.85557 17.7908 5.87087 18.2508 6.16567 18.5171C7.84174 20.031 10.0635 20.9531 12.5 20.9531C17.7158 20.9531 21.945 16.729 21.9531 11.5151ZM12.3516 5.60938C11.9676 5.60938 11.6562 5.9207 11.6562 6.3047V12.5234L14.9092 14.8892C15.2198 15.1151 15.6546 15.0464 15.8805 14.7358L16.0551 14.4957C16.281 14.1852 16.2123 13.7503 15.9018 13.5245L13.3438 11.6641V6.3047C13.3438 5.9207 13.0324 5.60938 12.6484 5.60938H12.3516Z" fill="white" />
                    </svg>

                    <p>Watch History</p>
                </div>

                <div  className="Card-Profile-Flex" >
                    <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M17 3C17 3.55228 16.5523 4 16 4H6C4.89543 4 4 4.89543 4 6V18C4 19.1046 4.89543 20 6 20H16C16.5523 20 17 20.4477 17 21C17 21.5523 16.5523 22 16 22H4C2.89543 22 2 21.1046 2 20V4C2 2.89543 2.89543 2 4 2H16C16.5523 2 17 2.44772 17 3Z" fill="#F84550" />
                        <path d="M21.2929 11.2929C21.6834 11.6834 21.6834 12.3166 21.2929 12.7071L17.75 16.25C17.3595 16.6404 16.7265 16.6404 16.336 16.25C15.9455 15.8595 15.9455 15.2264 16.336 14.836L18.172 13H9C8.44772 13 8 12.5523 8 12C8 11.4477 8.44771 11 9 11H18.172L16.336 9.16397C15.9455 8.7735 15.9455 8.14043 16.336 7.74997C16.7265 7.3595 17.3595 7.3595 17.75 7.74997L21.2929 11.2929Z" fill="#F84550" />
                    </svg>

                    <h2 onClick={loggout}>Unsubscribe</h2>
                </div>
            </div>
        </div>
    )
}


export default navmenu