import React from "react";
import { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { useParams } from "react-router-dom";
import { APIKey } from "../../../Services/APIKEY";
import "./SliderScript"
import "./FeatureSlider.css";



export default () => {  


    const image_path = "https://image.tmdb.org/t/p/original/";
    const { id } = useParams();
    const [movies, setMovies] = useState([]);
  

    useEffect(() => {
      fetch(`
      https://api.themoviedb.org/3/trending/all/day?api_key=${APIKey}&language=pt-BR&page=1`)
        .then((response) => response.json())
        .then((data) => {
          const moviesqtde = data.results;
          moviesqtde.length = 5;
          setMovies(moviesqtde);
        });
    }, []);

    



    return (

        <div className="feature-container">
            <span className="prev-btn" onClick={prevSlider}>
                <img src="https://github.com/Risxard/BD-Screens/blob/main/prev-btn.png?raw=true" alt="" />
            </span>

            <div className="slide-content">
                <div className="slider">
                    <input type="radio" name="slide" id="slide1 s1"/>
                    <input type="radio" name="slide" id="slide2" defaultChecked/>
                    <input type="radio" name="slide" id="slide3" />
                    <input type="radio" name="slide" id="slide4" />
                    <input type="radio" name="slide" id="slide5" />
                    <input type="radio" name="slide" id="slide6" />


                    <div className="slide s1" id="slide1"></div>

                    {movies.map((movie) => {


                    return (
                        <div className="slide" key={movie.id}>
                        <div className="feature-intro">
                            <div>
                                {movie.title == null ? <a href="#"><h2>{movie.name}</h2></a> : <a href="#"><h2>{movie.title}</h2></a>}
                                <h3> · {movie.release_date} - · 51m</h3>
                                <p>{movie.overview}</p>
                            </div>
 
                            <div className="feature-btn">
                                <Link to={`/BD-Screens/movie/${movie.id}`} className="playnow-btn">
                                        <div className="align-btn">
                                            <svg width="43" height="42" viewBox="0 0 43 42" fill="none" xmlns="http://www.w3.org/2000/svg">
                                                <path d="M21.5003 42C33.0704 42 42.4498 32.598 42.4498 21C42.4498 9.40202 33.0704 0 21.5003 0C9.93019 0 0.550781 9.40202 0.550781 21C0.550781 32.598 9.93019 42 21.5003 42Z" fill="#F41B3B" />
                                                <path d="M29.8023 19.5125L18.4546 12.95C17.9309 12.6 17.2326 12.6 16.7088 12.95C16.1851 13.3 15.8359 13.825 15.8359 14.4375V27.475C15.8359 28.0875 16.1851 28.7 16.7088 28.9625C16.9707 29.1375 17.3199 29.225 17.5817 29.225C17.8436 29.225 18.1928 29.1375 18.4546 28.9625L29.715 22.4C30.2388 22.05 30.5879 21.525 30.5879 20.9125C30.5879 20.3 30.326 19.775 29.8023 19.5125Z" fill="white" fillOpacity="0.87" />
                                            </svg>
                                            <p>Play Now</p>
                                        </div>
                                </Link>
                                <span className="watchlist-btn">
                                        <div className="align-btn">
                                            <svg width="20" height="20" viewBox="0 0 21 20" fill="none" xmlns="http://www.w3.org/2000/svg">
                                                <rect x="0.5" y="3.02734" width="14.4715" height="1.67378" rx="0.836888" fill="white" />
                                                <rect x="0.5" y="7.85156" width="14.4715" height="1.67378" rx="0.836888" fill="white" />
                                                <rect x="0.5" y="12.6758" width="9.64765" height="1.67378" rx="0.836888" fill="white" />
                                                <rect x="12.3164" y="13.4336" width="8.68296" height="1.67378" rx="0.836888" fill="white" />
                                                <rect x="17.5234" y="9.96094" width="8.68296" height="1.67378" rx="0.836888" transform="rotate(90 17.5234 9.96094)" fill="white" />
                                            </svg>
                                            <p>watchlist</p>
                                        </div>
                                </span>
                            </div>
                        </div>
    
                            <span className="filter"></span>
                            <img src={`${image_path}${movie.backdrop_path}`} alt={movie.title} />
                        </div>
                    );
                })}

                </div>
            </div>

            <span className="next-btn" onClick={nextSlider}>
                <img src="https://github.com/Risxard/BD-Screens/blob/main/prev-btn.png?raw=true" alt="" />
            </span>
        </div>
    )


    function prevSlider() {

        if (document.getElementById('slide3').checked === true){

            document.getElementById('slide2').checked = true
        }

        if (document.getElementById('slide4').checked === true){

            document.getElementById('slide3').checked = true
        }

        if (document.getElementById('slide5').checked === true){

            document.getElementById('slide4').checked = true
        }

        if (document.getElementById('slide6').checked === true){

            document.getElementById('slide5').checked = true
        }

    };

    function nextSlider() {

        if (document.getElementById('slide5').checked === true){

            document.getElementById('slide6').checked = true
        }

        if (document.getElementById('slide4').checked === true){

            document.getElementById('slide5').checked = true
        }

        if (document.getElementById('slide3').checked === true){

            document.getElementById('slide4').checked = true
        }

        if (document.getElementById('slide2').checked === true){

            document.getElementById('slide3').checked = true
        }
 
    };
}