import React from "react";
import { useState, useEffect } from "react";
import { APIKey } from "../../Services/APIKEY";

import './Search.css'


var Search = () => {

  const [movies, setMovies] = useState([]);
  const image_path = "https://image.tmdb.org/t/p/original/";
  useEffect(() =>{

    fetch(`
    https://api.themoviedb.org/3/discover/movie?include_adult=false&include_video=true&language=pt-BR&page=5&primary_release_year=2023&sort_by=vote_count.desc&api_key=${APIKey}`)
        .then((response) => response.json())
        .then((data) => {
          const moviesqtde = data.results;
          moviesqtde.length = 10;
          setMovies(moviesqtde);
    });
  
  }, [])




  return (
    <div className="Search">

    </div>
  );
};

export default Search;
