import React from "react";
import "../LiveShow/LiveShow.css";
import { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { useParams } from "react-router-dom";
import { APIKey } from "../../../Services/APIKEY";
import RatingStars from "./RatingStars/RaitingStarScript.jsx";

// api_key=${APIKey}

function Liveshow(liveShow) {
  const image_path = "https://image.tmdb.org/t/p/original/";

  const [movies, setMovies] = useState([]);
  const { id } = useParams();

  useEffect(() => {
    fetch(`
    https://api.themoviedb.org/3/discover/tv?include_adult=false&include_null_first_air_dates=false&language=pt-BR&page=1&sort_by=popularity.desc&with_original_language=pt&api_key=${APIKey}`)
      .then((response) => response.json())
      .then((data) => {
        const moviesqtde = data.results;
        moviesqtde.length = 9;
        setMovies(moviesqtde);
      });
  }, []);

  
  useEffect(() => {
    const nextBtn = document.getElementById("LiveShow-nextbtn");
    const prevBtn = document.getElementById("LiveShow-prevbtn");
    const slideList = document.querySelector(".ShowList");

    const scrollar = slideList.offsetWidth / 1.17;

    prevBtn.addEventListener("click", function () {
      slideList.scrollLeft -= scrollar;
    });

    nextBtn.addEventListener("click", function () {
      slideList.scrollLeft += scrollar;
    });
  }, []);

  return (
    <section className="livecontent">
      <div className="LiveshowTitle">
        <h2>Live Show</h2>

        <span className="SeeAll">
          <h3>See all</h3>
          <span className="SeeAllSvg">
            <svg
              width="16"
              height="17"
              viewBox="0 0 16 17"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M2.66536 9.24349L10.7787 9.24349L7.05203 12.9702L7.9987 13.9102L13.332 8.57682L7.9987 3.24349L7.0587 4.18349L10.7787 7.91016L2.66536 7.91016L2.66536 9.24349Z"
                fill="#F41B3B"
                stroke="#F41B3B"
                strokeWidth="0.2"
              />
            </svg>
          </span>
        </span>
      </div>

      <div className="LiveShowContent">
        <div id="LiveShow-prevbtn">
          <svg
            width="18"
            height="30"
            viewBox="0 0 18 30"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M0.447213 15.9685L15.4472 29.6045C16.0329 30.1371 16.9827 30.1371 17.5685 29.6044C18.1543 29.0719 18.1543 28.2085 17.5684 27.676L3.62912 15.0042L17.5685 2.33172C18.1543 1.79918 18.1543 0.935815 17.5684 0.40327C17.2756 0.136998 16.8917 0.00390649 16.5078 0.00390649C16.1239 0.00390649 15.74 0.136998 15.4471 0.403362L0.447213 14.0401C0.165813 14.2958 0.00781264 14.6426 0.00781264 15.0043C0.00781264 15.3659 0.165813 15.7128 0.447213 15.9685Z"
              fill="white"
            />
          </svg>
        </div>

        <div className="ShowListInner">
          <ul className="ShowList">
            {movies.map((movie) => {
              return (
                <li className="ShowItem" key={movie.id}>

                  <div className="hover-effect">
                    <Link to={`/BD-Screens/movie/${movie.id}`} className="btn-svg">
                      <svg width="63" height="62" viewBox="0 0 63 62" fill="none" xmlns="http://www.w3.org/2000/svg">
                      <path d="M31.8789 62C48.9997 62 62.8789 48.1208 62.8789 31C62.8789 13.8792 48.9997 0 31.8789 0C14.7581 0 0.878906 13.8792 0.878906 31C0.878906 48.1208 14.7581 62 31.8789 62Z" fill="#F41B3B"/>
                      <path d="M44.1589 28.8094L27.3672 19.1219C26.5922 18.6052 25.5589 18.6052 24.7839 19.1219C24.0088 19.6386 23.4922 20.4135 23.4922 21.3177V40.5636C23.4922 41.4677 24.0088 42.3719 24.7839 42.7594C25.1714 43.0177 25.688 43.1468 26.0755 43.1468C26.4631 43.1468 26.9797 43.0177 27.3672 42.7594L44.0297 33.0719C44.8047 32.5552 45.3214 31.7802 45.3214 30.876C45.3214 29.9719 44.9339 29.1969 44.1589 28.8094Z" fill="white" fillOpacity="0.87"/>
                      </svg>
                    </Link>

                    <div className="Hover-Info">
                      <span className="hoverTitle">
                        <span>
                          <Link to={`/BD-Screens/movie/${movie.id}`} style={{ textDecoration: 'none' }} className="TitleHover">
                            <h2 >{movie.name}</h2>
                          </Link>
                          <p>1h 14m</p>
                        </span>
                        <span className="LiveDownload">
                          <svg width="14" height="15" viewBox="0 0 14 15" fill="none" xmlns="http://www.w3.org/2000/svg">
                          <path fillRule="evenodd" clipRule="evenodd" d="M13.3001 8.61719C13.6866 8.61719 14 8.93056 14 9.31712L14 12.1168C14 12.6737 13.7788 13.2078 13.385 13.6016C12.9912 13.9954 12.4571 14.2166 11.9002 14.2166L2.10116 14.2166C1.54426 14.2166 1.01017 13.9954 0.616381 13.6016C0.222594 13.2078 0.00136644 12.6737 0.00136646 12.1168L0.00136658 9.31712C0.0013666 8.93056 0.314736 8.61719 0.701298 8.61719C1.08786 8.61719 1.40123 8.93056 1.40123 9.31712L1.40123 12.1168C1.40123 12.3025 1.47497 12.4805 1.60624 12.6118C1.7375 12.743 1.91553 12.8168 2.10116 12.8168L11.9002 12.8168C12.0858 12.8168 12.2639 12.743 12.3951 12.6118C12.5264 12.4805 12.6001 12.3025 12.6001 12.1168L12.6001 9.31712C12.6001 8.93056 12.9135 8.61719 13.3001 8.61719Z" fill="#1884F7"/>
                          <path fillRule="evenodd" clipRule="evenodd" d="M10.9942 5.32219C11.2676 5.59553 11.2676 6.03871 10.9942 6.31205L7.49456 9.8117C7.22122 10.085 6.77804 10.085 6.5047 9.8117L3.00504 6.31205C2.7317 6.0387 2.7317 5.59553 3.00504 5.32219C3.27838 5.04885 3.72156 5.04885 3.9949 5.32219L6.99963 8.32692L10.0044 5.32219C10.2777 5.04885 10.7209 5.04885 10.9942 5.32219Z" fill="#1884F7"/>
                          <path fillRule="evenodd" clipRule="evenodd" d="M6.99929 0.21875C7.38585 0.21875 7.69922 0.53212 7.69922 0.918682L7.69922 9.31786C7.69922 9.70442 7.38585 10.0178 6.99929 10.0178C6.61273 10.0178 6.29936 9.70442 6.29936 9.31786L6.29936 0.918682C6.29936 0.53212 6.61273 0.21875 6.99929 0.21875Z" fill="#1884F7"/>
                        </svg>

                          <Link to={`/BD-Screens/movie/${movie.id}`} style={{ textDecoration: 'none' }} className="TitleHover">
                            <p>Download</p>
                          </Link>
                        </span>
                      </span>
                    </div>
                    <div className="image-container">
                      <span className="image-filter"></span>
                      <img src={`${image_path}${movie.backdrop_path}`} alt={movie.title} />
                    </div>
                  </div>

                  <div className="LiveshowImgContainer">
                    <img
                      src={`${image_path}${movie.backdrop_path}`}
                      alt={movie.title}
                    />
                  </div>

                  <div className="LiveShowInfo">
                    <div className="LiveShowInfoTitle">
                      <h2>{movie.name}</h2>
                      <RatingStars
                        rating={movie.vote_average}
                        ratingVotes={movie.vote_count}
                      />
                    </div>
                  </div>
                </li>
              );
            })}
          </ul>
        </div>
        <div id="LiveShow-nextbtn">
          <svg
            width="18"
            height="30"
            viewBox="0 0 18 30"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M17.5606 14.0354L2.56057 0.399397C1.97487 -0.133148 1.02506 -0.133148 0.439263 0.399488C-0.146438 0.932033 -0.146438 1.7954 0.439363 2.32794L14.3787 14.9997L0.439263 27.6722C-0.146438 28.2047 -0.146438 29.0681 0.439363 29.6006C0.732164 29.8669 1.11606 30 1.49997 30C1.88387 30 2.26777 29.8669 2.56067 29.6005L17.5606 15.9638C17.842 15.7081 18 15.3613 18 14.9997C18 14.638 17.842 14.2911 17.5606 14.0354Z"
              fill="white"
            />
          </svg>
        </div>
      </div>
    </section>
  );
}

export default Liveshow;
