import MenuSlider from './MenuSlider/MenuSlider.jsx'
import FeatureSlider from "./FeatureSlider/FeatureSlider";
import BackgroundComponent from "./BackgroundComponent/BackgroundComponent"




export default props => {
    const logged = localStorage.getItem("statusLog");

    return (
        <header>
            {logged === "true" ?  <MenuSlider></MenuSlider> : ''}
            {logged === "true" ? <FeatureSlider></FeatureSlider> : <BackgroundComponent></BackgroundComponent>}
        </header>
    )}

