import React from "react";
import ReactDOM from "react-dom/client";
import { BrowserRouter, Routes, Route } from "react-router-dom";

import App from './App.jsx'
import Home from './Pages/Home.jsx'
import Movie from './Pages/Logged/Movie.jsx'
import Search from './Pages/Logged/Search.jsx'

import "./index.css";

const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(
    <BrowserRouter>
      <Routes>
        <Route element={<App />}>
          <Route path="/BD-Screens" element={<Home/>} />
          <Route path="/BD-Screens/movie/:id" element={<Movie/>} />
          <Route path="/BD-Screens/search" element={<Search/>} />
        </Route>
      </Routes>
    </BrowserRouter>
);
