import React from "react";
import { Link } from "react-router-dom";
import { useParams } from "react-router-dom";
import { useState, useEffect } from "react";
import { APIKey } from "../../Services/APIKEY";
import LoginPage from "./LoginPage";

import {ReactComponent as WatchlistIcon}  from './Icons/WatchlistIcon.svg'
import {ReactComponent as DownloadIcon} from './Icons/DownloadIcon.svg'
import {ReactComponent as ShareIcon} from './Icons/ShareIcon.svg'

import "./Movie.css";

var Movie = () => {
  const image_path = "https://image.tmdb.org/t/p/original/";
  let statusLog = localStorage.getItem("statusLog");

  const [movies, setMovies] = useState([]);
  const [moviesFor, setMoviesFor] = useState([]);
  const [moviesDetails, setMoviesDetails] = useState([]);
  const { id } = useParams();

  useEffect(() => {
          //  API de videos e detalhes
          fetch(`
          https://api.themoviedb.org/3/movie/${id}/videos?language=en-US&api_key=${APIKey}`)
            .then((response) => response.json())
            .then((data) => {
              const dados = data.results;
      
              let objetoProcurado = dados.find((dado) => dado.type === "Trailer");
      
              if (objetoProcurado) {
                setMovies(objetoProcurado);
      
              } else {
                console.log(
                  "Nenhum objeto com valor de Type igual a 'Trailer' foi encontrado."
                );
              }
            });

          fetch(`
          https://api.themoviedb.org/3/movie/${id}?language=en-US&api_key=${APIKey}`)
              .then((response) => response.json())
              .then((data) => {
                const moviesDetails = data;
                setMoviesDetails(moviesDetails);
    
          });

          //  API da section MoviesForYou

          fetch(`
          https://api.themoviedb.org/3/movie/top_rated?language=en-US&page=2&api_key=${APIKey}`)
              .then((response) => response.json())
              .then((data) => {
                const moviesqtde = data.results;
                moviesqtde.length = 6;
              setMoviesFor(moviesqtde);
          });



          window.scrollTo(0,0)

  }, []);




    // const setDate = date => {
    //     const partes = date.split('-');
    //     if (partes.length >= 1) {
    //       return partes[0];
    //     } else {
    //       return null;
    //     }
    // }

    if (statusLog === "true"){
      return (
        <div className="MovieContainer">
          <div className="MovieInner">
    
            <div className="Movie" key={movies.id}>
              <div className="MoviePlayer">
                <iframe
                  width="100%"
                  height="360"
                  src={`https://www.youtube.com/embed/${movies.key}?autoplay=1&modestbranding=1&color=white`}
                  title="YouTube video player"
                  frameBorder="0"
                  allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
                  allowFullScreen
                ></iframe>
              </div>

              <div className="MovieContent">
                <div className="MoviePanel">
                  <div className="MovieTitle">
                    <h2>{moviesDetails.title}</h2>
                    <h3>2018 • 2h 35m</h3>
                    <p>{moviesDetails.tagline}</p>
                  </div>
    
                  <div className="MovieBtns">
                    <span className="WatchlistIcon">
                      <WatchlistIcon/>
                      <p>Watchlist</p>
                    </span>
                    
                    <span className="ShareIcon">
                      <ShareIcon/>
                      <p>Share</p>
                    </span>

                    <span className="DownloadIcon">
                      <DownloadIcon/>
                      <p>Download</p>
                    </span>
                  </div>

                </div>
    
                <div className="MovieDescriptionContainer">
                  <h3>Description</h3>

                  <p>{moviesDetails.overview}</p>
                </div>
              </div>
            </div>
    
            <section className="MoviesForYou">
              <div className="MoviesForYouTitle">
                <h2>Movies for you</h2>
              </div>
    
              <div className="MovieList">
              {moviesFor.map((movie) => {
                              return (
                              <div className="MovieForItem" key={movie.id}>
                                <div className="MovieForCard-inner">
                                    <div className="MovieForCard-container">
                                            <div className="bg-filter"></div>
                                            <img src={`${image_path}${movie.poster_path}`} alt={movie.title} />
    
                                            <span className="MovieForItemTitle">
                                            <h3>{movie.title}</h3>
                                            </span>
    
                                            {   statusLog === "true" ? 
                                                    <Link to={`/BD-Screens/movie/${movie.id}`}>
                                                    <span className="MovieForCard-Btn">
                                                        <svg width="66" height="67" viewBox="0 0 66 67" fill="none" xmlns="http://www.w3.org/2000/svg">
                                                    <path d="M33 66.3181C51.0809 66.3181 65.7383 51.6253 65.7383 33.5008C65.7383 15.3764 51.0809 0.683594 33 0.683594C14.9192 0.683594 0.261719 15.3764 0.261719 33.5008C0.261719 51.6253 14.9192 66.3181 33 66.3181Z" fill="#F41B3B"/>
                                                    <path d="M45.9701 31.1813L28.2369 20.9258C27.4184 20.3789 26.3271 20.3789 25.5086 20.9258C24.6902 21.4729 24.1445 22.2932 24.1445 23.2504V43.6244C24.1445 44.5816 24.6902 45.5388 25.5086 45.949C25.9179 46.2224 26.4635 46.3591 26.8727 46.3591C27.282 46.3591 27.8276 46.2224 28.2369 45.949L45.8337 35.6936C46.6522 35.1466 47.1978 34.3262 47.1978 33.369C47.1978 32.4118 46.7886 31.5915 45.9701 31.1813Z" fill="white" fillOpacity="0.87"/>
                                                        </svg>
                                                    </span>
                                                    </Link> 
    
                                                    :
    
                                                    <span className="MovieForCard-Btn">
                                                    <svg width="66" height="67" viewBox="0 0 66 67" fill="none" xmlns="http://www.w3.org/2000/svg">
                                                    <path d="M33 66.3181C51.0809 66.3181 65.7383 51.6253 65.7383 33.5008C65.7383 15.3764 51.0809 0.683594 33 0.683594C14.9192 0.683594 0.261719 15.3764 0.261719 33.5008C0.261719 51.6253 14.9192 66.3181 33 66.3181Z" fill="#F41B3B"/>
                                                    <path d="M45.9701 31.1813L28.2369 20.9258C27.4184 20.3789 26.3271 20.3789 25.5086 20.9258C24.6902 21.4729 24.1445 22.2932 24.1445 23.2504V43.6244C24.1445 44.5816 24.6902 45.5388 25.5086 45.949C25.9179 46.2224 26.4635 46.3591 26.8727 46.3591C27.282 46.3591 27.8276 46.2224 28.2369 45.949L45.8337 35.6936C46.6522 35.1466 47.1978 34.3262 47.1978 33.369C47.1978 32.4118 46.7886 31.5915 45.9701 31.1813Z" fill="white" fillOpacity="0.87"/>
                                                    </svg>
                                                    </span>
                                                }
    
                                    </div>
    
                                </div>
                              </div>
                              );
                          })}
              </div>
    
            </section>
    
          </div>
    
        </div>
      );
    } 
    else {
      return (
        <LoginPage></LoginPage>
      )
    }
};

export default Movie;
