import React, { useEffect, useState } from "react";

import LiveShow from "./LiveShow/LiveShow";
import PopularMovies from "./PopularMovies/PopularMovies";
import BackdropSlider from '../SliderComponents/BackdropSlider/BackdropSlider.jsx';

import "./Main.css";

export default function Main() {
    const [sections, setSections] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const [visibleSections, setVisibleSections] = useState(4);
    const [movieDetails, setMoviesDetails] = useState([]);

    useEffect(() => {
        fetch('https://raw.githubusercontent.com/Risxard/BdScreenArray/main/Array.json')
            .then((response) => response.json())
            .then((data) => {
                setSections(data);
            });
    }, []);

    useEffect(() => {
        fetch('https://raw.githubusercontent.com/Risxard/BdScreenArray/main/Array.json')
            .then((response) => response.json())
            .then((data) => {
                setMoviesDetails(data);
            });
    }, []);

    useEffect(() => {
        const intersectionObserver = new IntersectionObserver((entries) => {
            if (entries.some((entry) => entry.isIntersecting)) {
                console.log('Elemento está visível!');
                setVisibleSections((prevVisibleSections) => prevVisibleSections + 4);
            }
        });
        intersectionObserver.observe(document.querySelector('#InfiniteCheck'));
        return () => intersectionObserver.disconnect();
    }, []);

    return (
        <main>
            <LiveShow></LiveShow>
            <PopularMovies></PopularMovies>

            {sections.slice(0, visibleSections).map((Section) => (
                <BackdropSlider id={Section.id} sectionTitle={Section.sectionTitle} genre={Section.genre} key={Section.id}></BackdropSlider>
            ))}

            <span id="InfiniteCheck"></span>
        </main>
    )
}